.app-link {
    color: #61dafb;
    padding: 30px;
    position: absolute;
    text-decoration: none;
}

p,
h1,
h2,
h3,
img {
    -webkit-touch-callout: none; /* iOS Safari */
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.leaflet-container {
    width: 100%;
    height: 91vh;
}

.leaflet-control-attribution {
    display: none;
}

/* Remove leaflet lines between tiles
https://github.com/Leaflet/Leaflet/issues/3575 */
.leaflet-tile-container img {
    width: 256.8px !important;
    height: 256.8px !important;
}

// Base class for hover icons
.hover-base {
    opacity: 0;

    -webkit-transition: opacity 0.2s ease-in-out;
    -moz-transition: opacity 0.2s ease-in-out;
    transition: opacity 0.2s ease-in-out;
}

.vertex-icon {
    @extend .hover-base;
    filter: brightness(175%) saturate(200%);
}

.vertex-icon:hover {
    opacity: 1;
}

.bullseye-icon {
    @extend .hover-base;
    background-image: url(images/bullseye-flat.svg);
    // filter: invert(27%) sepia(51%) saturate(2878%) hue-rotate(346deg) brightness(104%) contrast(97%);
    filter: invert(30%) sepia(40%) saturate(2878%);
}

.bullseye-icon:hover {
    opacity: 1;
}

.hover-icon {
    @extend .hover-base;
    filter: invert(130%);
}

.hover-icon:hover {
    opacity: 1;
}

.exit-icon {
    @extend .hover-base;
    background-image: url(images/exit.svg);
    filter: invert(100%);
}

.exit-icon:hover {
    opacity: 1;
}

.MuiDialog-paperScrollPaper {
    background-color: ivory;
}

.banner {
    position: absolute;
    left: 50%;
    display: block;
    margin: 100px -200px;
    width: 400px;
    height: 60px;
    border: 1px solid #b92;
    font: normal 30px/60px "MedievalSharp";
    text-align: center;
    color: black;
    background: #ca3;
    border-radius: 4px;
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.15) inset, 0 6px 10px rgba(0, 0, 0, 0.15);
}

.banner::before,
.banner::after {
    content: "";
    position: absolute;
    z-index: -1;
    left: -70px;
    top: 24px;
    display: block;
    width: 40px;
    height: 0px;
    border: 30px solid #ca3;
    border-right: 20px solid #a82;
    border-bottom-color: #c8a42e;
    border-left-color: transparent;
    transform: rotate(-5deg);
}

.banner::after {
    left: auto;
    right: -70px;
    border-left: 20px solid #a82;
    border-right: 30px solid transparent;
    transform: rotate(5deg);
}

.title {
    height: 5vh;
    padding: 20px 0;
}

.title-large {
    height: 10vh;
    padding: 20px 0;
}

.shadow {
    -webkit-filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
    filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.7));
    /* Similar syntax to box-shadow */
}
